<template>
<div class="asklist">
  <div class="q-top">
    <div class="time">
      <div>
        <mt-button size="small" @click="showDate(0)">开始时间</mt-button>
        <span class="date">{{startTime | formatDate}}</span>
      </div>
      <div>
        <mt-button size="small" @click="showDate(1)">结束时间</mt-button>
        <span class="date">{{overTime | formatDate}}</span>
      </div>
    </div>
    <div class="search">
      <div><input class="wfinput" v-model="historyValue.qmsj_project_title_filter" type="text" placeholder="项目名称"></div>
        <div><input class="wfinput" v-model="historyValue.qmsj_people_name_filter" type="text" placeholder="负责人"></div>
          <div>
            <mt-button size="small" type="primary" @click="changeSearch">搜索</mt-button>
          </div>
        </div>
      </div>

      <div class="q-main">
        <div v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" :infinite-scroll-immediate-check="true" infinite-scroll-distance="10">
          <div class="q-main-list" v-for="(v,i) in newsListData" :key="i" @click="goQuestionDetails(v.qmsj_respondent_id)">
            <div class="name">{{v.project_title}}</div>
            <div class="name">{{v.format_add_time_ymd}}<br/> {{v.format_add_time_hi}}</div>
            <div class="name">{{v.qmsj_people_name}} <br/>{{v.catagory}}</div>
          </div>
        </div>
        <div class="loading" v-if="isLoading">
          <div class="lcing">
            <mt-spinner class="cir" type="fading-circle" :size="30"></mt-spinner> 加载中
          </div>
        </div>
        <div class="loading" v-if="noMore">
          <div class="lcing">已全部加载</div>
        </div>
        <div class="loading" v-if="hasHistory">
          <div class="lcing">暂无数据</div>
        </div>
      </div>
      <div class="pickerpop" @touchmove.prevent>
        <mt-datetime-picker class="datefff" ref="picker" v-model="pickerDate" type="date" year-format="{value} 年" month-format="{value} 月" date-format="{value} 日" :closeOnClickModal="false" @confirm="confirmDate">
        </mt-datetime-picker>
      </div>
    </div>
</template>

<script>
import {
  formatDate
} from '@/assets/js/filter'
import {
  getReadRespondentList
} from '@/api/http'
export default {
  data() {
    return {
      newsListData: [],
      hasHistory: false, // 是否有记录
      loading: true, //为true时不会触发加载更多，false可以触发
      noMore: false, // 是否还有更多
      isLoading: false, // 加载中转菊花
      pickerDate: '', // 时间
      startTime: '', // 开始时间
      overTime: '', // 结束时间
      isStart: '', //  判断点击的是开始还是结束
      historyValue: {
        page: 1,
        count: "20",
        qmsj_project_title_filter: '',
        qmsj_people_name_filter: '',
        start_date_filter: '',
        end_date_filter: ''
      }
    }
  },
  methods: {
    async getReadRespondentList() {
      const res = await getReadRespondentList(this.historyValue)
      let resnewsListLength = res.read_respondent_list.length;
      this.isLoading = false;
      if (resnewsListLength == 0 && this.newsListData.length == 0) {
        this.loading = true;
        this.hasHistory = true
      } else {
        this.hasHistory = false
        //有数据并且请求到的数据等于pageSize说明数据库还有多余数据
        if (resnewsListLength === parseInt(this.historyValue.count)) {
          for (var i = 0; i < this.historyValue.count; i++) {
            this.newsListData.push(res.read_respondent_list[i]);
          }
          this.loading = false;
        } else {
          //有数据但是数据库数据已经被请求完，没有其他数据
          this.loading = true;
          this.noMore = true;
          for (var i = 0; i < resnewsListLength; i++) {
            this.newsListData.push(res.read_respondent_list[i]);
          }
        };
        //数据正好请求完，由于前面加了resnewsListLength == 0限制，会走当前逻辑
        if (resnewsListLength == 0) {
          this.loading = true;
          this.noMore = true;
        }
      }
    },
    changeSearch() {
      this.isMoreLoading = true
      this.historyValue.page = 1 // 初始化
      this.newsListData = []
      this.noMore = false
      this.getReadRespondentList()
    },
    loadMore() {
      this.historyValue.page++;
      this.loading = true;
      this.isLoading = true;
      this.getReadRespondentList();
    },
    showDate(isStart) {
      this.isStart = isStart
      this.pickerDate = new Date()
      this.$refs.picker.open();
    },
    confirmDate() {
      if (this.isStart == 0) {
        this.startTime = this.pickerDate
        this.historyValue.start_date_filter = this.pickerDate
      } else {
        this.overTime = this.pickerDate
        this.historyValue.end_date_filter = this.pickerDate
      }
    },
    goQuestionDetails(v){
      this.$router.push({
        name:'askDetails',
        params:{
          id:v
        }
      })
    }
  },
  filters: {
    formatDate(v) {
      if (v != '') {
        return formatDate(v, 'yyyy-MM-dd')
      } else {
        return ''
      }
    }
  },
  watch: {
    '$route': {
      immediate: true, // 确认是否以当前的初始值执行handler的函数
      handler() {
        this.getReadRespondentList();
      }
    }
  }

}
</script>

<style lang="less" scoped>
.asklist {
  .wfinput {
    width: 100%;
    height: 30px;
    padding: 0 10px;
    border: 1px solid #dcdfe6;
  }

  .q-top {
    position: fixed;
    top: 0;
    left: 0;
    width: 375px;
    font-size: 0;
    padding: 10px;
    background: #fff;
    height: 94px;

    .search {
      display: flex;
      align-items: center;

      >div {
        flex: 1;
        width: 0;
        margin-right: 10px;
      }
    }

    .time {
      display: flex;
      align-items: center;
      margin-bottom: 8px;

      >div {
        flex: 1;
        width: 0;

        .date {
          font-size: 14px;
          padding-left: 8px;
        }
      }
    }

  }

  .q-main {
    margin-top: 94px;
    padding: 0 10px;

    .q-main-list {
      display: flex;
      padding: 10px 0;
      align-items: center;
      border-bottom: 1px solid #dcdfe6;

      .name {
        flex: 1;
        width: 0;
        font-size: 14px;
      }

      .name:nth-child(2) {
        flex: 0 0 110px;
        text-align: center;
        padding: 0 8px;
      }

      .name:last-child {
        flex: 0 0 110px;
        text-align: center;
      }
    }

    .loading {
      padding: 20px 0;

      .lcing {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;

        .cir {
          margin-right: 8px;
        }
      }

    }
  }

}
</style><style>
.datefff {
  background-color: #fff !important;
  width: 100%;
}
</style>



